.news {
  max-width: 779px;

  &__list {
    display: flex;
    flex-direction: column;
    row-gap: 60px;
  }

  &__item {
  }

  &__article {
    display: flex;
    gap: 30px;

    @media (max-width: 990px) {
      flex-direction: column;
    }
  }
}

.article {
  &__image {
    max-height: 296px;
  }

  &__content {
  }

  &__header {
    padding-bottom: 9px;
  }

  &__list {
  }

  &__item {
  }

  &__category {
    padding: 10px 0;

    font-size: 14px;
    text-transform: uppercase;
    color: var(--green-color);
    font-weight: 700;

    @include hover {
      text-decoration: underline;
    }
  }

  &__title {
  }

  &__title-link {
  }

  &__info {
    padding: 29px 0;

    display: flex;
    gap: 12px;

    font-size: 12px;
  }

  &__date {
    color: var(--noactive-color);
    font-weight: 400;
  }

  &__autor {
    color: var(--black-color);
    font-weight: 700;
  }

  &__text-card {
    &:not(:first-child) {
      padding-top: 7px;
    }
  }

  &__descr {
    margin: 0;
    font-size: 15px;
    line-height: 22px;
    color: var(--black-color);
    font-weight: 400;

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__link {
    margin: 0;
  }
}
