.aside {
  max-width: 271px;

  @media (max-width: 990px) {
    max-width: fit-content;
    min-width: 270px;
  }

  @media (max-width: 640px) {
    display: none;
  }

  &__title {
    position: relative;

    margin: 0;

    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    color: var(--black-color);
    font-weight: 700;

    &::after {
      content: "";
      position: absolute;
      right: 50%;
      left: 50%;
      bottom: -15px;
      transform: translate(-50%, -50%);
      opacity: 0.4;
      width: 66px;
      height: 2px;
      background-color: var(--green-color);
    }
  }

  &__list {
    margin-top: 35px;

    display: flex;
    flex-direction: column;
    row-gap: 23px;
  }

  &__item {
  }

  &__article {
    display: flex;
    column-gap: 20px;
  }

  &__image {
    max-height: 70px;
  }

  &__form {
    margin-top: 30px;

    display: flex;

    font-size: 13px;
  }

  &__subscribe {
    margin-top: 30px;
  }

  &__form--email {
    flex-direction: column;
    row-gap: 20px;
  }

  &__tags {
    margin-top: 30px;
  }

  &__content {
    margin-top: 30px;

    display: flex;
    flex-wrap: wrap;
    gap: 5px;
  }

  &__content-item {
  }

  &__link {
    padding: 5px 10px;

    display: block;

    font-size: 12px;
    border: 3px solid #eeeeee;
    text-transform: uppercase;
    color: var(--black-color);
    font-weight: 700;

    &:hover {
      background-color: rgba(244, 243, 241, 0);
      border: 3px solid var(--green-color);
    }
  }

  &__categories {
    margin-top: 30px;
  }
}

.article {
  &__text {
  }

  &__date {
  }

  &__date-small {
    opacity: 0.541;
    font-size: 12px;
  }

  &__title-small {
    margin: 0;
  }

  &__title-black {
  }

  &__title-link {
  }
}

.form {
  &__label {
    width: 100%;
  }

  &__search {
    padding: 15px 10px;

    width: 100%;
    max-width: 222px;

    background-color: var(--placeholder-color);

    &:focus {
      border: 1px solid var(--green-color);
      outline: none;
    }
  }

  &__button {
    width: 100%;
    max-width: 48px;

    border: 0;
  }

  &__button-search {
    background: url("https://netology-code.github.io/html-2-diploma/sources/images/search.svg")
      center center / cover no-repeat;
    background-size: 20px 20px;
    background-color: var(--green-color);

    &:hover {
      background-color: var(--black-hover-color);
    }
  }

  &__email {
    padding: 15px 10px;

    width: 100%;
    max-width: 270px;

    background-color: var(--placeholder-color);

    &:focus {
      border: 1px solid var(--green-color);
      outline: none;
    }
  }

  &__button-email {
    margin-top: 30px;
    padding: 10px;

    width: 100%;
    max-width: 270px;

    font-size: 14px;
    line-height: 40px;
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 700;
    background-color: var(--green-color);

    &:hover {
      background-color: var(--black-hover-color);
    }
  }
}

.categories {
  &__list {
    width: 100%;
  }

  &__item {
    padding: 15px 0;
    border-bottom: 1px solid #eeeeee;
  }

  &__link {
    position: relative;

    font-size: 14px;
    color: #000000;
    font-weight: 700;

    &:hover {
      color: var(--green-color);
    }
  }

  &__link-index {
    font-size: 12px;
    color: var(--green-color);
    font-weight: 700;
    vertical-align: super;
  }
}

.title-small {
  font-size: 14px;
  line-height: 18px;
}

.categories__link .categories__link-index {
  font-size: 12px;
  color: var(--green-color);
  font-weight: 700;
  vertical-align: super;
}
