.section-offset {
  padding: 65px 0;

  @include big-tablet {
    padding: 50px 0;
  }

  @media (max-width: 640px) {
    padding: 20px 0;
  }
}

.article__title {
  margin: 0;
}

.article__title-link {
  font-size: clamp(1rem, 0.9403rem + 0.2581vw, 1.25rem);
  text-transform: uppercase;
  line-height: 24px;
  font-weight: 700;
  transition: 0.3s ease-in-out;

  @include hover {
    color: var(--green-color);
  }

  @media (max-width: 640px) {
    line-height: 20px;
  }
}

.article__title-black {
  color: var(--black-color);
}

.article__title-white {
  color: var(--light-color);
}

.article__link-category {
  font-size: 14px;
  text-transform: uppercase;
  color: var(--green-color);
  font-weight: 700;

  @include hover {
    text-decoration: underline;
  }
}

.article__list {
  display: flex;
  gap: 20px;
}
