.hero {
  position: relative;
  width: 100%;
  max-height: 450px;
  padding: 173px 0;
  text-align: center;
  background: url(".././img/banner-bg.jpg") center center / cover no-repeat;
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.55);
  }

  @media (min-width: 641px) and (max-width: 1200px) {
    background: url(".././img/banner-bg-tablet.jpg") center center / cover
      no-repeat;
  }

  @media (max-width: 640px) {
    max-height: 640px;
    padding: 230px 0;
    background: url(".././img/banner-mobile.jpg") center center / cover
      no-repeat;
  }

  &__container {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 45px;
  }

  &__logo {
    position: relative;
    z-index: 1;
    @media (min-width: 641px) {
      display: none;
    }
  }

  &__title {
    position: relative;
    z-index: 1;
    margin: 0 auto;

    max-width: 700px;

    font-size: clamp(1.625rem, 1.3415rem + 1.2258vw, 2.8125rem);
    line-height: 75px;
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 700;

    @media (max-width: 640px) {
      line-height: 36px;
    }
  }
}
