.trends {
  padding: 55px 0 0;
  &__container {
  }

  &__title {
    position: relative;
    margin: 0;

    font-size: 20px;
    line-height: 29px;
    text-transform: uppercase;
    color: var(--black-color);
    font-weight: 700;
    text-align: center;

    &::after {
      content: "";
      position: absolute;
      right: 50%;
      left: 50%;
      bottom: -10px;
      transform: translate(-50%, -50%);
      opacity: 0.4;
      width: 66px;
      height: 3px;
      background-color: var(--green-color);
    }
  }

  &__wrapper {
    padding-top: 40px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, minmax(295px, 1fr));
    gap: 5px;

    // @media (max-width: 1670px) {
    //   grid-template-columns: repeat(4, minmax(350px, 1fr));
    // }

    // @media (max-width: 1420px) {
    //   grid-template-columns: repeat(4, minmax(300px, 1fr));
    // }

    // @media (max-width: 1220px) {
    //   grid-template-columns: repeat(4, minmax(295px, 1fr));
    // }

    @media (max-width: 1200px) {
      grid-template-columns: repeat(2, minmax(315px, 1fr));
    }

    @media (max-width: 640px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    position: relative;
    min-height: 319px;

    display: flex;
    align-items: end;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.55);
    }

    @include small-tablet {
      min-height: 247px;
    }
  }

  &__item-first {
    background: url(".././img/trend1.jpg") center center / cover no-repeat;
  }

  &__item-second {
    background: url(".././img/trend2.jpg") center center / cover no-repeat;
  }

  &__item-third {
    background: url(".././img/trend3.jpg") center center / cover no-repeat;
  }

  &__item-fourth {
    background: url(".././img/trend4.jpg") center center / cover no-repeat;
  }

  &__article {
    position: relative;
    z-index: 1;

    display: flex;
    flex-direction: column;
    gap: 9px;
    width: 100%;

    margin: 0 20px 15px;
    padding: 33px 35px;
    border: 3px solid var(--green-color);

    @media (max-width: 1220px) {
      padding: 35px 30px;
    }
  }
}

